:root {
  --heights: 50vh;
  --widths: 100%;
}
.slider-container {
  height: 50vh;
  height: var(--heights);
  width: 100%;
  width: var(--widths);
  position: relative;
  margin: auto;
  overflow: hidden;
}
.active {
  display: inline-block;
}
.inactive {
  display: none;
}
.slides {
  height: 50vh;
  height: var(--heights);
  width: var(--width);
  position: relative;
}
.slide-image {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
}
.slide-title, .slide-text {
  width: 100%;
  height: 100%;
  color: white;
  font-size: 50px;
  position: absolute;
  text-align: center;
  top: 40%;
  z-index: 10;
}
.slide-text {
  top: 65%;
  font-size: 2rem;
}
.prev, .next {
  cursor: pointer;
  z-index: 100;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 1rem;
  margin-top: -3rem;
  font-size: 40px;
  font-weight: bold;
  border-radius: 0px 5px 5px 0px;
}
.prev:hover, .next:hover {
  color: white;
  background-color: rgba(0,0,0,0.6);
  transition: all 0.5s ease-in;
}
.next {
  right: 0;
  border-radius: 5px 0px 0px 5px;
}
.all-dots {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  top: 85%;
  justify-content: center;
  z-index: 200;
}
.dot {
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  margin: 0px 3px;
  background-color: rgba(0,0,0,0.3);
  border-radius: 50%;
  display: inline-block;
}
.active-dot, .dot:hover {
  background-color: rgba(255,255,255,0.5);
}
#float-label {
  display: flex;
  flex-direction: column;
  /*min-width: 350px;*/
  position: relative;
  padding: 20px 30px;
  background-color: rgb(109, 104, 104);
}
#float-label input {
  /*width: 100%;*/
  height: 56px;
  padding: 14px 16px 0 10px;
  outline: 0;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}
#float-label label {
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  padding: 0 12px;
  color: #999;
  pointer-events: none;
  position: absolute;
  transform: translate(0, 26px) scale(1);
}
#float-label:focus-within label {
  transform: translate(0, 12px) scale(0.75);
}
#float-label .Active {
  transform: translate(0, 12px) scale(0.75);
}
